import MasterPage from '../components/Master';
import fecth from 'isomorphic-unfetch';
import Banner from '../components/Banner';
import News from '../components/News';

const Home = (props) => (


    <MasterPage>
        <div className='row'>
            <div className='col-md-12'>
                <Banner />
            </div>
            <div className='col-md-8'>
                <h2>Last news</h2>
                <News  news={props.articles} />
            </div>
            <div className='col-md-4'>
                <h2>Last events</h2>
            </div>
        </div>
    </MasterPage>
)

Home.getInitialProps = async () => {
    const res = await fecth(`https://newsapi.org/v2/top-headlines?country=us&category=technology&apiKey=bd9f21cf0af34d709850de2b448cb767`);
    const resEvents = await fecth(`https://www.eventbriteapi.com/v3/technology&token=G7IIBEZP2UGJLL7BNNAD`);

    const events = await resEvents.json();
    const news = await res.json();

    return {
        news,
        events
    };

}

export default Home